@import url(https://fonts.googleapis.com/css?family=Quicksand:300,500);
@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
body {
  margin: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html,
body,
#app,
#app > div {
  height: 100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login-page {
  width: 360px;
  padding: 8% 0 0;
  margin: auto;
  font-family: "Quicksand", sans-serif;
}
.login-page h1 {
  font-size: 40px;
  color: "#1e1e6e";
}
.form {
  position: relative;
  z-index: 1;
  background: #ffffff;
  max-width: 360px;
  height: 400px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form input {
  font-family: "Quicksand", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.form button {
  font-family: "Quicksand", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #1e1e6e;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #ffffff;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form button:hover,
.form button:active,
.form button:focus {
  background: #589dd2;
}
.form .message {
  margin: 15px 0 0;
  color: #b3b3b3;
  font-size: 12px;
}
.form .message a {
  color: #589dd2;
  text-decoration: none;
}
.form .register-form {
  display: none;
}
.container {
  position: relative;
  z-index: 1;
  max-width: 300px;
  margin: 0 auto;
}
.container:before,
.container:after {
  content: "";
  display: block;
  clear: both;
}
.container .info {
  margin: 50px auto;
  text-align: center;
}
.container .info h1 {
  margin: 0 0 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 300;
  color: #1a1a1a;
}
.container .info span {
  color: #4d4d4d;
  font-size: 12px;
}
.container .info span a {
  color: #000000;
  text-decoration: none;
}
.container .info span .fa {
  color: #ef3b3a;
}
body {
  /* background: #91b0ff; fallback for old browsers */

  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: center;
  font-family: "Ubuntu", sans-serif;
  height: 100%;
  margin: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* body {
  background-color: white;
} */

.title {
  color: #91b0ff;
}
.datatable {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 50px;
  font-size: 1rem;
}

.datatable td,
#datatable th {
  border: 1px solid #ddd;
  padding: 8px;
}

.datatable tr:nth-child(even) {
  background-color: #dbe5ff;
}

.datatable tr:hover {
  background-color: #ddd;
}

.datatable th {
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 5px;
  text-align: left;
  background-color: #1e1e6e;
  color: white;
}

.grid-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  text-align: center;
  padding-top: 40px;
}

.banner {
  width: 100vw;
  height: 70vh;
  background-color: #1e1e6e;
}

.summary {
  width: 93vw;
  height: 37vh;
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
  background-color: white;
  align-content: center;
  box-shadow: 0 4px 8px 0 rgb(0, 0, 0, 0.2);
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  font-family: "Quicksand", sans-serif;
  font-weight: 1000;
  color: #91b0ff;
  border: solid 1px #1e1e6e;
}

.stat {
  min-width: 30vw;
  height: 35vh;
  display: flex;
  margin: 10px;
  background-color: white;
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
  align-content: center;
  box-shadow: 0 4px 8px 0 rgb(0, 0, 0, 0.2);
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  font-family: "Ubuntu", sans-serif;
  font-weight: 1000;
  color: #91b0ff;
  border: solid 1.5px #1e1e6e;
}

#homeTitle {
  font-family: "Ubuntu", sans-serif;
  padding-top: 40px;
  font-weight: 1000;
  color: white;
  margin: 0;
}

.stat:hover {
  box-shadow: 0 8px 16px 0 rgb(0, 0, 0, 0.2);
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  text-decoration: none !important;
}

.stats {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  text-align: center;
  /* margin-top: -20vh; */
}

.p-icon {
  /* max-height: 100px; */
  max-width: 30vw;
  width: 150px;
}

#icons {
  align-items: center;
  justify-content: space-around;
  display: flex;
  flex-flow: row wrap;
}

#icons p {
  max-width: 30vw;
  font-family: "Ubuntu", sans-serif;
  font-size: 10px;
  font-weight: 900;
  color: #245db3;
}

.nav-item {
  padding: 2rem !important;
  cursor: pointer;
}
.navbar-brand {
  padding: 2rem !important;
}
div.nav-item:hover {
  animation: bounce 2.5s infinite;
  -webkit-animation: bounce 2.5s infinite;
  -moz-animation: bounce 2.5s infinite;
  -o-animation: bounce 2.5s infinite;
}

@-webkit-keyframes bounce {
  0%,
  100% {
    -webkit-transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-10px);
  }
}
@keyframes bounce {
  0%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

.mybutton {
  background-color: #1e1e6e;
  border: none;
  border-radius: 8px;
  color: white;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 12px;
  margin: 15px 2px 30px;
  cursor: pointer;
}

.mybutton:disabled {
  background-color: rgba(34, 34, 114, 0.493);
}
.buttonContainer {
  width: 100vw;
  display: flex;
  justify-content: center;
}

.buttonContainer h5 {
  padding: 20px;
}

